import React from 'react';
import styles from "./HomePage.module.css"

const TITLES = ['Publications', 'Projects', 'Results', 'CV']
const NUMBER_OF_WORDS = TITLES.length
const CHANGING_TIME = 3000
var openAboutMe = false
var closeAboutMe = false
class HomePage extends React.Component{

    constructor(props){
        super(props)
        this.changingWord = React.createRef()
        this.state = {
            currentIdx: 0,
            aboutMeActive: false
        }
    }
    
    componentDidMount(){
        setTimeout(() => {
            openAboutMe = true 
        }, 5000)
        this.intervalId = setInterval(() => {
            this.setState({
                currentIdx: (this.state.currentIdx + 1)%NUMBER_OF_WORDS
            })
            this.changingWord.current.classList.remove(styles.activeAnimText)
            void this.changingWord.current.offsetWidth;
            this.changingWord.current.classList.add(styles.activeAnimText)
        }, CHANGING_TIME)
    }

    componentWillUnmount(){
        clearInterval(this.intervalId)
    }

    handleMouseOver = (evt) => {
        const aboutMe = evt.target
        if (!this.state.aboutMeActive && openAboutMe){
            closeAboutMe = false
            setTimeout(() => {
                closeAboutMe = true 
            }, 2000)
            aboutMe.classList.add(styles.active)
            aboutMe.classList.remove(styles.inactive)
            aboutMe.childNodes[1].classList.add(styles.activeText)
            const textList = document.querySelectorAll('#text1, #text2, #text3')
            textList.forEach((elt) => {
                elt.classList.add(styles.centralTextActive)
            })
            this.setState({
                aboutMeActive: !this.state.aboutMeActive
            })
        } 
    }

    handleClick = (evt) => {
        const aboutMe = evt.target
        if (this.state.aboutMeActive && closeAboutMe){
            openAboutMe = false
            setTimeout(() => {
                openAboutMe = true 
            }, 2000)
            aboutMe.classList.remove(styles.active)
            aboutMe.classList.add(styles.inactive)
            aboutMe.childNodes[1].classList.remove(styles.activeText)
            const textList = document.querySelectorAll('#text1, #text2, #text3')
            textList.forEach((elt) => {
                elt.classList.remove(styles.centralTextActive)
            })
            this.setState({
                aboutMeActive: !this.state.aboutMeActive
            })
        }
    }

    handleMouseLeave = (evt) => {
        evt.target.classList.remove(styles.active)
        evt.target.childNodes[1].classList.remove(styles.activeText)
        const textList = document.querySelectorAll('#text1, #text2, #text3')
        textList.forEach((elt) => {
            elt.classList.remove(styles.centralTextActive)
        })
    }

    render(){
        return (
            <div className = {styles.container}>
                <p className = {styles.text1} id = 'text1'>On This Website</p>
                <p className = {styles.text2} id = 'text2'> You Can Check My</p>
                <p 
                    className = {styles.changingText} 
                    id = 'text3'
                    ref = {this.changingWord}> 
                        {TITLES[this.state.currentIdx]} 
                </p>
                
                <div className = {styles.textRotate}
                    onMouseEnter = {(evt) => {this.handleMouseOver(evt)}}
                    onMouseLeave = {(evt) => {this.handleClick(evt)}}> 
                    ABOUT ME 
                    <div className = {styles.subText}>
                        
                        Hello I'm Pierre Cardi, a PhD Student in Computer Sciences at Dauphine.<br/>
                        Passionate about sciences, I completed my course in fundamental mathematics 
                        by obtaining the external aggregation.
                        <br/>
                        Willing to put theory into practice, I then moved towards a more applied field: 
                        computer science. Now, I am designing algorithms 
                        for computational social choices. <br/>
                        <br/>
                        Besides computer science, I am fond of music. I obtained the "3ème cycle" degree 
                        in piano (highest French amateurs degree) and am a self-taught guitar player.
                    </div>
                </div>
            </div>
            
        )
    }
}

export default HomePage;