import React,{Component} from 'react'
import { createPortal } from 'react-dom';
import './App.css';
import HomePage from './components/HomePage/HomePage';
import Projects from './components/Projects/Projects';
import Publications from './components/Publications/Publications';
import CV from './components/CV/CV';


const TRANSPARANCY = 0.6
const TEXT_COLOR = ['lightblue', 'rgba(100, 60, 60, 1)', 'oldLace','rgb(80, 100, 80)']
const TEXT_COLOR_TRANSPARENT = [`rgba(173,216,230,${TRANSPARANCY})`, `rgba(100, 60, 60, ${TRANSPARANCY})`, `rgba(253,245,230, ${TRANSPARANCY})`,`rgba(80, 100, 80,${TRANSPARANCY})`]
const BACK_COLOR = ['rgb(40, 40, 40)','rgba(255, 200, 180, 1)', 'lightSeaGreen','lightgreen']
const COLOR_NUMBER = TEXT_COLOR.length


class App extends Component{

  state = {
    currentIdx: 0,
    currentPage: <HomePage/>
  }

  handlePageChange = (evt, newPage) => {

    const shape = document.getElementById('fancyShape')
    if (evt.target.id === 'home') {
      shape.classList.remove('activeShape')
    } else {
      shape.classList.add('activeShape')
    }
    if (this.state.currentActiveButton){
      this.state.currentActiveButton.classList.remove('active')
    }
    this.state.currentActiveButton.childNodes[1].classList.remove('active')
    evt.target.childNodes[1].classList.add('active')
    this.setState({
      currentPage: newPage,
      currentActiveButton: evt.target
    })
  }

  handleEnter = (evt) => {
    evt.target.childNodes[1].classList.add('active')
  }

  handleLeave = (evt) => {
    if (evt.target.id === this.state.currentActiveButton.id){
      return
    }
    evt.target.childNodes[1].classList.remove('active')
  }

  componentDidMount(){
    window.addEventListener('click', () => {
      console.log('hello')
      this.setState({
        currentIdx: (this.state.currentIdx + 1)%COLOR_NUMBER
      })
    })

    const menuButton = document.getElementById('home')
    this.setState({
      currentActiveButton: menuButton
    })
  }

  componentDidUpdate(){
    document.documentElement.style.setProperty('--text-color', TEXT_COLOR[this.state.currentIdx])
    document.documentElement.style.setProperty('--back-color', BACK_COLOR[this.state.currentIdx])
    document.documentElement.style.setProperty('--text-color-transparent', TEXT_COLOR_TRANSPARENT[this.state.currentIdx])
  }

  render(){
    return (
      <div className = 'app'>
        <header className = 'header'>
          <div 
            className = 'menu-button home unselectable' 
            id = 'home'
            onClick = {(evt) => {this.handlePageChange(evt, <HomePage/>)}}
            onMouseEnter = {(evt) => this.handleEnter(evt)}
            onMouseLeave = {(evt) => this.handleLeave(evt)}> 
              Home
              <div className = 'circle'/> 
          </div>
          <div className = "second-part-header">
            <div 
              className = 'my-button unselectable'
              id = 'projects'
              onClick = {(evt) => {this.handlePageChange(evt, <Projects/>)}}
              onMouseEnter = {(evt) => this.handleEnter(evt)}
              onMouseLeave = {(evt) => this.handleLeave(evt)}> 
                Projects
                <div className = 'circle'/>
            </div>
            <div className = 'vertical-bar'/>
            <div 
              className = 'my-button center unselectable'
              id = 'publications'
              onClick = {(evt) => {this.handlePageChange(evt, <Publications/>)}}
              onMouseEnter = {(evt) => this.handleEnter(evt)}
              onMouseLeave = {(evt) => this.handleLeave(evt)}> 
                Publications
                <div className = 'circle'/> 
              </div>
            <div className = 'vertical-bar'/>
            <div className = 'my-button unselectable'
              id = 'CV'
              onClick = {(evt) => {this.handlePageChange(evt, <CV/>)}}
              onMouseEnter = {(evt) => this.handleEnter(evt)}
              onMouseLeave = {(evt) => this.handleLeave(evt)}> 
              CV 
              <div className = 'circle'/>
            </div>
          </div>
        </header>
        {this.state.currentPage}
        <div className = 'fancyShape' id = 'fancyShape'/>
      </div>
    )
  }
}

export default App;
