import React, {Component} from "react"
import styles from "./ProjectItem.module.css"

class ProjectItem extends Component{
    render(){
        const myClass = this.props.translate? (styles.translatedContainer):(styles.container)
        return(
            <div className = {myClass}>
                <header className = {styles.header}>
                    <a href={this.props.link} target="_blank">
                        {this.props.projectName}
                    </a>
                </header>
                
                <img className = {styles.imageStyle} src = {this.props.image}  width="45%" height="45%"/>
                <footer className = {styles.footer}>
                    {
                        this.props.technologies.map((tech, index) => {
                            return <img src = {tech} width = "8%" height = "60%"/> }
                        )
                    }
                </footer>
            </div>
        )
    }
}

export default ProjectItem